import React, {useState} from "react";
import s from "./Paginator.module.css";
import cn from "classnames";

let Paginator = ({totalItemsCount, pageSize, currentPage, onPageChanged, portionSize = 10}) => {

  console.log('–ö–æ–ª–∏—á–µ—Å—Ç–≤–æ –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª–µ–π');
  console.log(totalItemsCount);
  console.log('–¢–µ–∫—É—â–∞—è —Å—Ç—Ä–∞–Ω–∏—Ü–∞ –≤ –∫–æ–º–ø–æ–Ω–µ–Ω—Ç–µ –ü–∞–≥–∏—Ç–Ω–∞—Ç–æ—Ä');
  console.log(currentPage);
  let pagesCount = Math.ceil(totalItemsCount / pageSize);
  console.log('–ö–æ–ª–∏—á–µ—Å—Ç–≤–æ —Å—Ç—Ä–∞–Ω–∏—Ü');
  console.log(pagesCount);

	let pages = [];
	//–û—Ç–æ–±—Ä–∞–∂–∞–µ–º —Ç–æ–ª—å–∫–æ 20 —Å—Ç—Ä–∞–Ω–∏—Ü –∏–∑ 3000, –ø–æ—Ç–æ–º—É —á—Ç–æ –Ω–µ –∑–Ω–∞—é, –∫–∞ —Å–¥–µ–ª–∞—Ç—å –∫–æ–º–ø–æ–Ω–µ–Ω—Ç –ø–∞–≥–∏–Ω–∞—Ü–∏–∏
	for (let i = 1; i <= pagesCount; i++) {
		//for (let i = 1; i <= 20; i++) {
		pages.push(i);
  }
  
  let portionCount = Math.ceil(pagesCount / portionSize); //—Å—á–∏—Ç–∞–µ–º –∫–æ–ª–∏—á–µ—Å—Ç–≤–æ –ø–æ—Ä—Ü–∏–π - –∫–æ–ª–∏—á–µ—Å—Ç–≤–æ —Å—Ç—Ä–∞–Ω–∏—Ü—ã —Å —ç–ª–µ–º–µ–Ω—Ç–∞–º–∏ –Ω–∞ –∫–æ–ª–∏—á–µ—Å—Ç–≤–æ –ø–æ—Ä—Ü–∏–π
  let [portionNumber, setPortionNumber] = useState(1);
  let leftPortionPageNumber = (portionNumber - 1) * portionSize + 1;
  let rightPortionPageNumber = portionNumber * portionSize;

  return <div className={s.paginator}>
    {portionNumber > 1 &&
      <button onClick={() => { setPortionNumber(portionNumber - 1) }}>PREV</button> }
    {pages.filter(p => p >= leftPortionPageNumber && p <= rightPortionPageNumber)
      .map((p) => {
        return <span className={ cn({
            [s.selectedPage]: currentPage === p
          }, s.pageNumber)}
            key={p}
            onClick={(e) => {
              onPageChanged(p);
            }}>{p}</span>
                  })}
      {portionCount > portionNumber &&
        <button onClick={() => { setPortionNumber(portionNumber + 1) }}>Next</button>}
      </div>
};

export default Paginator;
