// itemId - –∑–Ω–∞—á–µ–Ω–∏–µ –Ω–∞ —Å—Ä–∞–≤–Ω–µ–Ω–∏–µ, –∫–æ—Ç–æ—Ä–æ–µ, —Å–∫–æ—Ä–µ–µ –≤—Å–µ–≥–æ, –ª–µ–∂–∏—Ç –≤ action

export const updateObjectInArray = (
	items,
	itemId,
	objPropName,
	newObjProps
) => {
	return items.map((user) => {
		if (user[objPropName] === itemId) {
			return {...user, ...newObjProps};
		}
		return user;
	});
};
